<template>
  <section>
    <div class="searchBar">
        <input class="searchInput" type="search" v-model="search" placeholder="search country">
    </div>
   <div class="table-content">
    <table>
        <thead>
            <tr >
                <th v-for="(col,index) in column" :key="index">
                    {{ col }}
                </th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(count,index) in filterred" :key="index" class="tr">
                <td >{{ count.Country }}</td>
                <td>{{ count.NewConfirmed }}</td>
                <td>{{ count.TotalConfirmed }}</td>
                <td>{{ count.NewDeaths }}</td>
                <td>{{ count.TotalDeaths }}</td>
            </tr>
        </tbody>
    </table>
   </div>
  </section>
</template>


<script>
import axios from 'axios';
export default {
    name:'CountryWise',
    data(){
        return{
            column:["Country","New Cases", "Toatal Cases","New Death", "Total Death"],
            country:[],
            search:'',
        }
      },
      computed:{
        filterred:function(){
            return this.country.filter((count)=>{
                return count.Country.toLowerCase().match(this.search)
            })
        }
      },
      mounted(){
        axios
         .get('https://api.covid19api.com/summary')
         .then(response =>(
          this.country = response.data.Countries
          ))
      },
}
</script>

<style scoped>

section{
    background: white;
}
.table-content{
    padding: 25px;
}

table{
    text-align: left;
    min-width: 900px ;
    border-collapse:collapse;
    margin: auto;
}

th{
    font-weight: bold;
    font-size: 24px;
    color:rgba(0, 0, 0, 0.5);
    padding: 20px 10px;
    border: none;
}
.tr{
    border-bottom:  0.5px solid #d1d1d1;
    text-align: center;
}
td:nth-child(1){
    text-align: left;
}

th:last-child{
    color:#FF7272
}

td{
    font-size: 20px;
    font-weight:200;
    padding: 30px 10px;
}
.tr:hover{
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.237);
    border-radius: 16px;
}
.searchBar{
    display:flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 20px 50px;
}

input[type=search]{
    width: 100%;
    padding: 20px;
    border-radius: 8px;
    border: 0.25px solid rgba(99, 101, 99, 0.733);
}
input[type=search]:focus {
    outline: none;
    border: 1px solid rgba(72, 147, 231, 0.733);
}

@media screen and (max-width:768px) {

    table{
        text-align: left;
        min-width: 260px;
        padding:20px;
    }
    th{
        font-weight: bold;
        font-size: 18px;
        padding: 10px 5px;
    }
    td{
        font-size: 16px;
        font-weight:200;
        padding: 10px 0px;
    }
}
</style>